@media screen and (max-width: 1400px), screen and (max-device-width: 1400px){
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch{
          .search{
            margin-top: 40px;
          }
          .numbers {
            margin-top: 35px;
            ul {
              li {
                i {
                  width: 45px;
                  height: 45px;
                }
              }
            }
          }
          .workTime {
            margin: 40px 0 0 15px;
          }
        }
        .header__cabinet{
          margin-top: 40px;
          ul{
            li{
              &:nth-child(3){
                width: 210px;
                height: 30px;
                i{
                  margin: 0!important;
                }
              }
            }
          }
        }
        .header__secondNav{
          bottom: -250px;
        }
      }
    }
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          .actualProduct__slider,
          .saleProduct__slider{
            img{
              width: 150px;
              height: 290px;
            }
            .slider{
              width: 1010px;
              height: 290px;
              .lSSlideOuter{
                ul{
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 290px;
                  }
                  li{
                    img{
                      width: 150px;
                      height: 150px;
                    }
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder{
          div{
            .info{
              margin-left: 55px;
            }
          }
        }
      }
    }
    .content__addres{
      iframe{
        height: 465px;
      }
      .addres__contactForm{
        .contactForm__comentary{
          input[type*="text"]{
            width: 184px;
          }
          input[type*="textarea"]{
            width: 395px;
          }
        }
        .contactForm__contacts{
          width: 75%;
        }
      }
    }
  }
}
